/* MovieDetailsPage.module.css */
.movieDetailsContainer {
    display: flex;
    margin: 20px;
}

.image {
    width: 300px;
    height: auto;
    margin-right: 20px;
}

.movieDetailsWrap {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.buttonContainer {
    display: flex;
    justify-content:left;
    padding-top: 100px;

    /* Center the buttons horizontally */
    /* Add some space below the buttons */
}

.link {
    display: inline-block;
    align-content: center;
    margin-right: 10px;
    text-decoration: none;
    color: #000;
}

.header {
    display: flex;
    justify-content: left;
    margin: 0 100px;
    text-align: center;
    line-height: 20px;;
}

.infoButton{
    margin-right: 15px;;
}

.goBackButton,
.infoButton {
    display: block;
    margin: 0 auto;
    padding: 10px 20px;
    color:white;
    background-color: #dc3545;
    text-decoration: none;
    border-radius: 5px;
    border: none;
    cursor: pointer;
    transition: background-color 0.2s;
    font-weight: 600;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
}

.goBackButton:hover,
.infoButton:hover {
    color: #faf5f4;
    background-color: rgb(184, 41, 41);
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.4);
}

.goBackLink {
    display: block;
    margin-top: 50px;
    text-decoration: none;
}

